<template>
    <div class="nationList">
        <header-common :headText="title"></header-common>
        <!-- 栏目详情 -->
        <div class="nation contentMain">
            <ul class="clearfix">
                <li @click="subjectSearch(item)" v-for="(item,i) in childNationList" :key="i" class="nationItem">
                    <div class="nationItemMain">
                        <div class="nationName">{{item.codename}}</div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import headerCommon from '../../common/headerCommon.vue';
export default {
    name: 'list',
    data() {
        return {
            title: '',
            childNationList:[],
        }
    },
    components:{"header-common":headerCommon},
    watch: {
        "$route.fullPath": {
            handler() {
                this.toggleLang();
                this.$router.push({ name: this.langType != 'cn' ? this.langType + "nation" : "nation" });
            }
        }
    },
    mounted: function () {
        this.init();
    },
    methods: {
        init() {
            this.toggleLang();
            if(this.$route.params.dataObj){
                this.title = this.$route.params.dataObj.codename;
                this.getNationList();
            }else{
                this.$router.push({ name: this.langType != 'cn' ? this.langType + "nation" : "nation" });
            }
        },
        getNationList(){
            this.commonMethods.emitAjax({
                path:"/api/front/codes/key/" + this.$route.params.dataObj.codekey + "/codes",
                success:(response)=>{
                    this.childNationList = response.content?response.content:[];
                }
            })
        },
        back() {
            this.$router.go(-1)//回退
        },
        subjectSearch: function (v) {
            var v = v || {};
            if (v.codekey != '' || v.codekey != null && v.codename != '') {
                var vuexParams_ = {
                    workkey: v.codekey,
                    name: v.codename
                };
                var params = {
                    subjects: v.codekey
                };
                this.$router.push({ name: this.langType != "cn" ? this.langType + 'search' : 'search' });
                this.$store.commit('clearkeySerach'); //清空关键字 
                this.$store.commit('setflg', false); //标记
                this.$store.commit("setxuekeparams", vuexParams_); //参数给vuex     
                this.$store.commit("search_More", params);
            }
        },
        toggleLang(){
            const _this = this;
            this.commonMethods.selectLangType(function (langType) {
                _this.lang = _this.langOption[langType];
                _this.langType = langType;
            })
        }
    }
}
</script>



